.contador{
    width: 100%;
    height: 18.75rem;
    font-size:10rem;
    padding: 1.563rem;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border: .5rem solid white;
    margin-bottom: 0.938rem;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
}
@media only screen and (max-width:768px) {
    
    .contador{
        width: 45%;
    }
}
