*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #1e0233;
}
.App{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding-top: 0.938rem;
    color: white;
    text-shadow: 0 0 .3rem white;
}
.contenedor-principal{
    height: 34.375rem;
    min-width:37.5rem ;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
   
}

.freecodecamp-logo-contenedor{
    /* border-style: solid; */
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1.25rem;
}
.freecodecamp-logo{
    height: 3.125rem;
    margin-top: 2rem;
}


/* ------------------------------------ */
/* responsive */

@media only screen and (max-width:768px) {
    
    .freecodecamp-logo{
        height: 2rem;
    }
}